import React, { Component } from "react";
import { baseUrl, key } from "./config";
import _ from "lodash";
import axios from "axios";
import { campos } from "./camposImovel";

import FotosImovel from "./components/imovel/fotos";
import Informacoes from "./components/imovel/info";

import detalhesdoimovel from "./assets/detalhesdoimovel.png";
import detalhesdocondominio from "./assets/detalhesdocondominio.png";
import pontosdeinteresse from "./assets/pontosdeinteresse.png";
import { pontos } from "./pontos";

// Font Awesome
import { library } from "@fortawesome/fontawesome-svg-core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { fab } from "@fortawesome/free-brands-svg-icons";
library.add(fab, fas);

function distance(lat1, lon1, lat2, lon2) {
  var R = 6371; // km (change this constant to get miles)
  var dLat = ((lat2 - lat1) * Math.PI) / 180;
  var dLon = ((lon2 - lon1) * Math.PI) / 180;
  var a =
    Math.sin(dLat / 2) * Math.sin(dLat / 2) +
    Math.cos((lat1 * Math.PI) / 180) *
      Math.cos((lat2 * Math.PI) / 180) *
      Math.sin(dLon / 2) *
      Math.sin(dLon / 2);
  var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
  var d = R * c;
  d = Math.round(d * 1000);
  d = Math.round(d / 10) * 10;
  return d;
}

class Imovel extends Component {
  state = {
    descricao: false,
    campos: [],
    photosLength: 0,
    content: {
      pt: {
        quartos: "Quartos",
        vagas: "Vagas",
        status: "",
        tipo: ""
      },
      en: {
        quartos: "Bedrooms",
        vagas: "Parking Spaces",
        status: "",
        tipo: ""
      }
    },
    imovel: {
      Foto: [{ Foto: "", FotoPequena: "" }],
      InfraEstrutura: [],
      Caracteristicas: [],
      Pontos: [],
      Status: []
    },
    currentPhoto: 0,
    fields: campos
  };
  getImovel = async codigo => {
    try {
      const fields = JSON.stringify(this.state.fields);
      const getImovel = await axios.get(
        `${baseUrl}/imoveis/detalhes?key=${key}&imovel=${codigo}&pesquisa={"fields":${fields}}`
      );
      const imovel = getImovel.data;
      // Transforma o objeto Fotos em arranjo
      const fotos = _.values(imovel.Foto);
      // Coloca foto em destaque como a primeira do arranjo
      imovel.Foto = _.sortBy(fotos, function(foto) {
        return foto.Destaque === "Sim" ? 0 : 1;
      });

      // Transforma objeto Caracteristicas em arranjo
      let Caracteristicas = _.map(_.toPairs(imovel.Caracteristicas), d =>
        _.fromPairs([d])
      );
      // Filtra apenas Caracteristicas que constam no imóvel
      Caracteristicas = Caracteristicas.filter(c => {
        return _.valuesIn(c)[0] === "Sim";
      });
      // Transforma objetos em strings
      Caracteristicas = Caracteristicas.map((i, v) => {
        return _.keysIn(i)[0];
      });
      imovel.Caracteristicas = Caracteristicas;

      // Transforma objeto InfraEstrutura em arranjo
      let InfraEstrutura = _.map(_.toPairs(imovel.InfraEstrutura), d =>
        _.fromPairs([d])
      );
      // Filtra apenas InfraEstrutura que constam no imóvel
      InfraEstrutura = InfraEstrutura.filter(c => {
        return _.valuesIn(c)[0] === "Sim";
      });
      // Transforma objetos em strings
      InfraEstrutura = InfraEstrutura.map((i, v) => {
        return _.keysIn(i)[0];
      });
      imovel.InfraEstrutura = InfraEstrutura;

      // Cria string com endereço completo do imóvel
      const Endereco = `${imovel.TipoEndereco} ${imovel.Endereco}, ${
        imovel.Numero
      } - São Paulo, ${imovel.Bairro}`;
      imovel.Endereco = Endereco;
      // Pega coordenadas geográficas a partir de endereço
      const getCoordinates = await axios.get(
        `https://maps.googleapis.com/maps/api/geocode/json?address=${encodeURI(
          Endereco
        )}&sensor=true&key=AIzaSyAly1CjP-Ek0BU7OoRfWP6-jflYyNe_h30`
      );
      const coordinates = await getCoordinates.data.results[0].geometry
        .location;
      imovel.Latitude = coordinates.lat;
      imovel.Longitude = coordinates.lng;

      // Filtra pontos de interesse a menos de 1500 metros do imóvel
      let Pontos = pontos.map(ponto => {
        const distancia = distance(
          parseFloat(ponto.LATITUDE),
          parseFloat(ponto.LONGITUDE),
          parseFloat(imovel.Latitude),
          parseFloat(imovel.Longitude)
        );
        if (distancia < 1000) {
          return { name: ponto.LOCAL, distance: distancia };
        }
      });
      Pontos = Pontos.filter(ponto => {
        return ponto !== undefined;
      });
      imovel.Pontos = _.orderBy(Pontos, ["distance", "name"], ["asc", "asc"]);

      console.log(imovel);
      this.setState({ imovel });
    } catch (error) {
      console.log(error.message);
    }
  };

  componentDidMount() {
    this.getImovel(this.props.match.params.codigo);
    window.scrollTo(0, 0);
  }
  _handlePhotoSelect = foto => {
    const fotos = this.state.imovel.Foto;
    const currentPhoto = fotos.indexOf(foto);
    this.setState({ currentPhoto });
  };
  _handlePhotoChange = direction => {
    const numberOfPhotos = this.state.imovel.Foto.length - 1;
    let currentPhoto = this.state.currentPhoto;
    if (direction === "next") {
      if (currentPhoto === numberOfPhotos) {
        currentPhoto = 0;
      } else {
        currentPhoto++;
      }
    } else {
      if (currentPhoto === 0) {
        currentPhoto = numberOfPhotos;
      } else {
        currentPhoto--;
      }
    }
    this.setState({ currentPhoto });
  };
  _openDescricao = () => {
    const descricao = !this.state.descricao;
    this.setState({ descricao });
  };
  render() {
    const { imovel, currentPhoto, content, descricao } = this.state;
    const { lang } = this.props;
    return (
      <div className="imovel">
        <div className="container">
          <div className="row">
            <div className="col-lg-8">
              <FotosImovel
                onPhotoChange={this._handlePhotoChange}
                onPhotoSelect={this._handlePhotoSelect}
                imovel={imovel}
                currentPhoto={currentPhoto}
              />
              <h1>
                {imovel.Categoria} - {imovel.Bairro}
              </h1>
              <p>
                {imovel.ValorVenda > 0 ? <strong>Valor de venda: </strong> : ""}
                {imovel.ValorVenda > 0
                  ? new Intl.NumberFormat("pt-BR", {
                      style: "currency",
                      currency: "BRL"
                    }).format(imovel.ValorVenda) + " | "
                  : ""}
                {imovel.ValorLocacao > 0 ? (
                  <strong>Valor de locação: </strong>
                ) : (
                  ""
                )}
                {imovel.ValorLocacao > 0
                  ? new Intl.NumberFormat("pt-BR", {
                      style: "currency",
                      currency: "BRL"
                    }).format(imovel.ValorLocacao) + " | "
                  : ""}
                Ref.: {imovel.Codigo}
              </p>
              <p className="imovel__caracteristicas">
                <FontAwesomeIcon icon={["fas", "bed"]} className="mr-1" />
                <strong>{imovel.Dormitorios}</strong> {content[lang].quartos}
                <FontAwesomeIcon icon={["fas", "car"]} className="mr-1 ml-3" />
                <strong>{imovel.Vagas}</strong> {content[lang].vagas}
                <FontAwesomeIcon icon={["fas", "bath"]} className="mr-1 ml-3" />
                <strong>{imovel.TotalBanheiros}</strong> Banheiros
                <FontAwesomeIcon
                  icon={["fas", "expand"]}
                  className="mr-1 ml-3"
                />
                <strong>{imovel.AreaPrivativa}</strong>m²
              </p>
              <div className="imovel__descricao">
                <div
                  className={
                    descricao
                      ? "imovel__descricao_container open"
                      : "imovel__descricao_container"
                  }
                >
                  <p>{imovel.DescricaoWeb}</p>
                </div>
                <span
                  onClick={this._openDescricao}
                  className="imovel__descricao_trigger"
                >
                  Leia mais
                </span>
              </div>

              <Informacoes imovel={imovel} />

              {imovel.Caracteristicas.length > 0 ? (
                <div className="imovel__infra">
                  <h3>
                    <img src={detalhesdoimovel} alt="" />
                    Detalhes do Imóvel
                  </h3>
                  {imovel.Caracteristicas.map(item => {
                    return (
                      <div key={`carac-${item}`} className="imovel__infra_item">
                        {item}
                      </div>
                    );
                  })}
                </div>
              ) : (
                ""
              )}

              {imovel.InfraEstrutura.length > 0 ? (
                <div className="imovel__infra">
                  <h3>
                    <img src={detalhesdocondominio} alt="" />
                    Detalhes do Condomínio
                  </h3>
                  {imovel.InfraEstrutura.map(item => {
                    return (
                      <div key={`condo-${item}`} className="imovel__infra_item">
                        {item}
                      </div>
                    );
                  })}
                </div>
              ) : (
                ""
              )}
              {imovel.Pontos.length > 0 ? (
                <div className="imovel__infra">
                  <h3>
                    <img src={pontosdeinteresse} alt="" />
                    Pontos de Interesse
                  </h3>
                  <small>
                    Pontos de interesse a menos de um quilômetro do imóvel
                    (distância aproximada)
                  </small>
                  <ul>
                    {imovel.Pontos.map(ponto => {
                      return (
                        <li key={`ponto-${ponto.distance}`}>
                          {ponto.name} - {ponto.distance}m
                        </li>
                      );
                    })}
                  </ul>
                </div>
              ) : (
                ""
              )}
            </div>
            <div className="col-lg-4 ">
              <div className="imovel__contato sticky-top">
                <h4>Gostou desse imóvel?</h4>
                <p>Envie uma mensagem!</p>
                <form>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Nome"
                  />
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Telefone"
                  />
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Email"
                  />
                  <textarea
                    name=""
                    id=""
                    cols="30"
                    rows="4"
                    className="form-control"
                    placeholder={`Olá, estou interessado no imóvel ${
                      imovel.Codigo
                    } e gostaria de receber mais informações.`}
                  />
                  <button className="btn btn-danger form-control">
                    Enviar Mensagem
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Imovel;
