import React, { Component } from "react";
import informacoes from "../../assets/informacoes.png";

class Informacoes extends Component {
  state = {};
  render() {
    const { imovel } = this.props;
    const ValorCondominio = (
      <li>
        <strong>Condomínio: </strong>
        {new Intl.NumberFormat("pt-BR", {
          style: "currency",
          currency: "BRL"
        }).format(imovel.ValorCondominio)}
      </li>
    );
    const ValorLocacao = (
      <li>
        <strong>Valor de Locação: </strong>
        {new Intl.NumberFormat("pt-BR", {
          style: "currency",
          currency: "BRL"
        }).format(imovel.ValorLocacao)}
      </li>
    );
    const ValorVenda = (
      <li>
        <strong>Valor de Venda: </strong>
        {new Intl.NumberFormat("pt-BR", {
          style: "currency",
          currency: "BRL"
        }).format(imovel.ValorVenda)}
      </li>
    );
    const ValorIptu = (
      <li>
        <strong>IPTU: </strong>
        {new Intl.NumberFormat("pt-BR", {
          style: "currency",
          currency: "BRL"
        }).format(imovel.ValorIptu)}
      </li>
    );
    return (
      <div className="imovel__infra">
        <h3>
          <img src={informacoes} alt="" />
          Informações
        </h3>
        <ul>
          <li>
            <strong>Referência: </strong> {imovel.Codigo}
          </li>
          <li>
            <strong>Finalidade: </strong> {imovel.Status}
          </li>
          <li>
            <strong>Bairro: </strong> {imovel.Bairro}
          </li>
          <li>
            <strong>Tipo do imóvel: </strong> {imovel.Categoria}
          </li>
          <li>
            <strong>Área Privativa: </strong> {imovel.AreaPrivativa}m²
          </li>
          <li>
            <strong>Área Total: </strong> {imovel.AreaTotal}m²
          </li>
          {imovel.ValorVenda > 0 ? ValorVenda : ""}
          {imovel.ValorLocacao > 0 ? ValorLocacao : ""}
          {imovel.ValorCondominio > 0 ? ValorCondominio : ""}
          {imovel.ValorIptu > 0 ? ValorIptu : ""}
          <li>
            <strong>Vagas: </strong> {imovel.Vagas}
          </li>
          <li>
            <strong>Quartos: </strong> {imovel.Dormitorios}
          </li>
          <li>
            <strong>Suites: </strong> {imovel.Suites}
          </li>
          <li>
            <strong>Banheiros: </strong> {imovel.TotalBanheiros}
          </li>
        </ul>
      </div>
    );
  }
}

export default Informacoes;
